ottom-left-radius: 4px;\r\n}\r\n\r\n.fc-state-default.fc-corner-right { /* non-theme\r\n\tborder-top-right-radius: 4px;\r\n\tborder-bottom-right-radius: 4px;\r\n}*/\r\n\r\n/* icons in buttons */\r\n\r\n.fc button .fc-icon {\r\n  /* non-theme */\r\n  position: relative;\r\n  top: -0.05em; /* seems to be a good adjustment across browsers */\r\n  margin: 0 0.2em;\r\n  vertical-align: middle;\r\n}\r\n\r\n/*\r\n  button states\r\n  borrowed from twitter bootstrap (http://twitter.github.com/bootstrap/)\r\n*/\r\n\r\n// .fc-state-default {\r\n// \tbackground-color: #f5f5f5;\r\n// \tbackground-image: -moz-linear-gradient(top, #ffffff, #e6e6e6);\r\n// \tbackground-image: -webkit-gradient(linear, 0 0, 0 100%, from(#ffffff), to(#e6e6e6));\r\n// \tbackground-image: -webkit-linear-gradient(top, #ffffff, #e6e6e6);\r\n// \tbackground-image: -o-linear-gradient(top, #ffffff, #e6e6e6);\r\n// \tbackground-image: linear-gradient(to bottom, #ffffff, #e6e6e6);\r\n// \tbackground-repeat: repeat-x;\r\n// \tborder-color: #e6e6e6 #e6e6e6 #bfbfbf;\r\n// \tborder-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.25);\r\n// \tcolor: #333;\r\n// \ttext-shadow: 0 1px 1px rgba(255, 255, 255, 0.75);\r\n// \tbox-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);\r\n// }\r\n\r\n.fc-state-hover,\r\n.fc-state-down,\r\n.fc-state-active,\r\n.fc-state-disabled {\r\n  color: #333333;\r\n  background-color: #e6e6e6;\r\n}\r\n\r\n.fc-state-hover {\r\n  color: #333333;\r\n  text-decoration: none;\r\n  background-position: 0 -15px;\r\n  -webkit-transition: background-position 0.1s linear;\r\n  -moz-transition: background-position 0.1s linear;\r\n  -o-transition: background-position 0.1s linear;\r\n  transition: background-position 0.1s linear;\r\n}\r\n\r\n.fc-state-down,\r\n.fc-state-active {\r\n  background-color: #cccccc;\r\n  background-image: none;\r\n}\r\n\r\n.fc-state-disabled {\r\n  cursor: default;\r\n  background-image: none;\r\n  opacity: 0.65;\r\n  box-shadow: none;\r\n}\r\n\r\n/* Buttons Groups\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-button-group {\r\n  display: inline-block;\r\n}\r\n\r\n/*\r\nevery button that is not first in a button group should scootch over one pixel and cover the\r\nprevious button's border...\r\n*/\r\n\r\n.fc .fc-button-group > * {\r\n  /* extra precedence b/c buttons have margin set to zero */\r\n  float: left;\r\n  margin: 0 0 0 2px;\r\n}\r\n\r\n.fc .fc-button-group > :first-child {\r\n  /* same */\r\n  margin-left: 0;\r\n}\r\n\r\n/* Popover\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-popover {\r\n  position: absolute;\r\n  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);\r\n}\r\n\r\n.fc-popover .fc-header {\r\n  padding: 2px 4px;\r\n}\r\n\r\n.fc-popover .fc-header .fc-title {\r\n  margin: 0 2px;\r\n}\r\n\r\n.fc-popover .fc-header .fc-close {\r\n  cursor: pointer;\r\n}\r\n\r\n.fc-ltr .fc-popover .fc-header .fc-title,\r\n.fc-rtl .fc-popover .fc-header .fc-close {\r\n  float: left;\r\n}\r\n\r\n.fc-rtl .fc-popover .fc-header .fc-title,\r\n.fc-ltr .fc-popover .fc-header .fc-close {\r\n  float: right;\r\n}\r\n\r\n/* unthemed */\r\n\r\n.fc-unthemed .fc-popover {\r\n  border-width: 1px;\r\n  border-style: solid;\r\n}\r\n\r\n.fc-unthemed .fc-popover .fc-header .fc-close {\r\n  font-size: 0.9em;\r\n  margin-top: 2px;\r\n}\r\n\r\n/* jqui themed */\r\n\r\n.fc-popover > .ui-widget-header + .ui-widget-content {\r\n  border-top: 0; /* where they meet, let the header have the border */\r\n}\r\n\r\n/* Misc Reusable Components\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-divider {\r\n  border-style: solid;\r\n  border-width: 1px;\r\n}\r\n\r\nhr.fc-divider {\r\n  height: 0;\r\n  margin: 0;\r\n  padding: 0 0 2px; /* height is unreliable across browsers, so use padding */\r\n  border-width: 1px 0;\r\n}\r\n\r\n.fc-clear {\r\n  clear: both;\r\n}\r\n\r\n.fc-bg,\r\n.fc-bgevent-skeleton,\r\n.fc-highlight-skeleton,\r\n.fc-helper-skeleton {\r\n  /* these element should always cling to top-left/right corners */\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n}\r\n\r\n.fc-bg {\r\n  bottom: 0; /* strech bg to bottom edge */\r\n}\r\n\r\n.fc-bg table {\r\n  height: 100%; /* strech bg to bottom edge */\r\n}\r\n\r\n/* Tables\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc table {\r\n  width: 100%;\r\n  box-sizing: border-box; /* fix scrollbar issue in firefox */\r\n  table-layout: fixed;\r\n  border-collapse: collapse;\r\n  border-spacing: 0;\r\n  font-size: 1em; /* normalize cross-browser */\r\n}\r\n\r\n.fc th {\r\n  text-align: center;\r\n}\r\n\r\n.fc th,\r\n.fc td {\r\n  border-style: solid;\r\n  border-width: 1px;\r\n  padding: 0;\r\n  vertical-align: top;\r\n}\r\n\r\n.fc td.fc-today {\r\n  border-style: double; /* overcome neighboring borders */\r\n}\r\n\r\n/* Internal Nav Links\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\na[data-goto] {\r\n  cursor: pointer;\r\n}\r\n\r\na[data-goto]:hover {\r\n  text-decoration: underline;\r\n}\r\n\r\n/* Fake Table Rows\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc .fc-row {\r\n  /* extra precedence to overcome themes w/ .ui-widget-content forcing a 1px border */\r\n  /* no visible border by default. but make available if need be (scrollbar width compensation) */\r\n  border-style: solid;\r\n  border-width: 0;\r\n}\r\n\r\n.fc-row table {\r\n  /* don't put left/right border on anything within a fake row.\r\n\t   the outer tbody will worry about this */\r\n  border-left: 0 hidden transparent;\r\n  border-right: 0 hidden transparent;\r\n\r\n  /* no bottom borders on rows */\r\n  border-bottom: 0 hidden transparent;\r\n}\r\n\r\n.fc-row:first-child table {\r\n  border-top: 0 hidden transparent; /* no top border on first row */\r\n}\r\n\r\n/* Day Row (used within the header and the DayGrid)\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-row {\r\n  position: relative;\r\n}\r\n\r\n.fc-row .fc-bg {\r\n  z-index: 1;\r\n}\r\n\r\n/* highlighting cells & background event skeleton */\r\n\r\n.fc-row .fc-bgevent-skeleton,\r\n.fc-row .fc-highlight-skeleton {\r\n  bottom: 0; /* stretch skeleton to bottom of row */\r\n}\r\n\r\n.fc-row .fc-bgevent-skeleton table,\r\n.fc-row .fc-highlight-skeleton table {\r\n  height: 100%; /* stretch skeleton to bottom of row */\r\n}\r\n\r\n.fc-row .fc-highlight-skeleton td,\r\n.fc-row .fc-bgevent-skeleton td {\r\n  border-color: transparent;\r\n}\r\n\r\n.fc-row .fc-bgevent-skeleton {\r\n  z-index: 2;\r\n}\r\n\r\n.fc-row .fc-highlight-skeleton {\r\n  z-index: 3;\r\n}\r\n\r\n/*\r\nrow content (which contains day/week numbers and events) as well as \"helper\" (which contains\r\ntemporary rendered events).\r\n*/\r\n\r\n.fc-row .fc-content-skeleton {\r\n  position: relative;\r\n  z-index: 4;\r\n  padding-bottom: 2px; /* matches the space above the events */\r\n}\r\n\r\n.fc-row .fc-helper-skeleton {\r\n  z-index: 5;\r\n}\r\n\r\n.fc-row .fc-content-skeleton td,\r\n.fc-row .fc-helper-skeleton td {\r\n  /* see-through to the background below */\r\n  background: none; /* in case <td>s are globally styled */\r\n  border-color: transparent;\r\n\r\n  /* don't put a border between events and/or the day number */\r\n  border-bottom: 0;\r\n}\r\n\r\n.fc-row .fc-content-skeleton tbody td, /* cells with events inside (so NOT the day number cell) */\r\n.fc-row .fc-helper-skeleton tbody td {\r\n  /* don't put a border between event cells */\r\n  border-top: 0;\r\n}\r\n\r\n/* Scrolling Container\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-scroller {\r\n  -webkit-overflow-scrolling: touch;\r\n}\r\n\r\n.fc-scroller > .fc-day-grid,\r\n.fc-scroller > .fc-time-grid {\r\n  position: relative; /* re-scope all positions */\r\n  width: 100%; /* hack to force re-sizing this inner element when scrollbars appear/disappear */\r\n}\r\n\r\n/* Global Event Styles\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-event {\r\n  position: relative; /* for resize handle and other inner positioning */\r\n  display: block; /* make the <a> tag block */\r\n  font-size: 0.85em;\r\n  line-height: 1.3;\r\n  border-radius: 2px;\r\n  background-color: $brand-success; /* default BACKGROUND color */\r\n  // @include shadow-big-color($brand-success);\r\n  font-weight: normal; /* undo jqui's ui-widget-header bold */\r\n\r\n  &.event-azure {\r\n    background-color: $brand-info;\r\n    // @include shadow-big-color($brand-info);\r\n  }\r\n  &.event-green {\r\n    background-color: $brand-success;\r\n    // @include shadow-big-color($brand-success);\r\n  }\r\n  &.event-orange {\r\n    background-color: $brand-warning;\r\n    // @include shadow-big-color($brand-warning);\r\n  }\r\n  &.event-red {\r\n    background-color: $brand-danger;\r\n    // @include shadow-big-color($brand-danger);\r\n  }\r\n  &.event-rose {\r\n    // background-color: $brand-rose;\r\n    // @include shadow-big-color($brand-rose);\r\n  }\r\n  &.event-default {\r\n    background-color: $default-color;\r\n    // @include shadow-big-color($gray-light);\r\n  }\r\n}\r\n\r\n.fc-event-dot {\r\n  background-color: #3a87ad; /* default BACKGROUND color */\r\n}\r\n\r\n/* overpower some of bootstrap's and jqui's styles on <a> tags */\r\n.fc-event,\r\n.fc-event:hover,\r\n.ui-widget .fc-event {\r\n  color: $white-color; /* default TEXT color */\r\n  text-decoration: none; /* if <a> has an href */\r\n}\r\n\r\n.fc-event[href],\r\n.fc-event.fc-draggable {\r\n  cursor: pointer; /* give events with links and draggable events a hand mouse pointer */\r\n}\r\n\r\n.fc-not-allowed, /* causes a \"warning\" cursor. applied on body */\r\n.fc-not-allowed .fc-event {\r\n  /* to override an event's custom cursor */\r\n  cursor: not-allowed;\r\n}\r\n\r\n.fc-event .fc-bg {\r\n  /* the generic .fc-bg already does position */\r\n  z-index: 1;\r\n  background: $white-color;\r\n  opacity: 0.25;\r\n}\r\n\r\n.fc-event .fc-content {\r\n  position: relative;\r\n  z-index: 2;\r\n}\r\n\r\n/* resizer (cursor AND touch devices) */\r\n\r\n.fc-event .fc-resizer {\r\n  position: absolute;\r\n  z-index: 4;\r\n}\r\n\r\n/* resizer (touch devices) */\r\n\r\n.fc-event .fc-resizer {\r\n  display: none;\r\n}\r\n\r\n.fc-event.fc-allow-mouse-resize .fc-resizer,\r\n.fc-event.fc-selected .fc-resizer {\r\n  /* only show when hovering or selected (with touch) */\r\n  display: block;\r\n}\r\n\r\n/* hit area */\r\n\r\n.fc-event.fc-selected .fc-resizer:before {\r\n  /* 40x40 touch area */\r\n  content: \"\";\r\n  position: absolute;\r\n  z-index: 9999; /* user of this util can scope within a lower z-index */\r\n  top: 50%;\r\n  left: 50%;\r\n  width: 40px;\r\n  height: 40px;\r\n  margin-left: -20px;\r\n  margin-top: -20px;\r\n}\r\n\r\n/* Event Selection (only for touch devices)\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-event.fc-selected {\r\n  z-index: 9999 !important; /* overcomes inline z-index */\r\n  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.fc-event.fc-selected.fc-dragging {\r\n  box-shadow: 0 2px 7px rgba(0, 0, 0, 0.3);\r\n}\r\n\r\n/* Horizontal Events\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n/* bigger touch area when selected */\r\n.fc-h-event.fc-selected:before {\r\n  content: \"\";\r\n  position: absolute;\r\n  z-index: 3; /* below resizers */\r\n  top: -10px;\r\n  bottom: -10px;\r\n  left: 0;\r\n  right: 0;\r\n}\r\n\r\n/* events that are continuing to/from another week. kill rounded corners and butt up against edge */\r\n\r\n.fc-ltr .fc-h-event.fc-not-start,\r\n.fc-rtl .fc-h-event.fc-not-end {\r\n  margin-left: 0;\r\n  border-left-width: 0;\r\n  padding-left: 1px; /* replace the border with padding */\r\n  border-top-left-radius: 0;\r\n  border-bottom-left-radius: 0;\r\n}\r\n\r\n.fc-ltr .fc-h-event.fc-not-end,\r\n.fc-rtl .fc-h-event.fc-not-start {\r\n  margin-right: 0;\r\n  border-right-width: 0;\r\n  padding-right: 1px; /* replace the border with padding */\r\n  border-top-right-radius: 0;\r\n  border-bottom-right-radius: 0;\r\n}\r\n\r\n/* resizer (cursor AND touch devices) */\r\n\r\n/* left resizer  */\r\n.fc-ltr .fc-h-event .fc-start-resizer,\r\n.fc-rtl .fc-h-event .fc-end-resizer {\r\n  cursor: w-resize;\r\n  left: -1px; /* overcome border */\r\n}\r\n\r\n/* right resizer */\r\n.fc-ltr .fc-h-event .fc-end-resizer,\r\n.fc-rtl .fc-h-event .fc-start-resizer {\r\n  cursor: e-resize;\r\n  right: -1px; /* overcome border */\r\n}\r\n\r\n/* resizer (mouse devices) */\r\n\r\n.fc-h-event.fc-allow-mouse-resize .fc-resizer {\r\n  width: 7px;\r\n  top: -1px; /* overcome top border */\r\n  bottom: -1px; /* overcome bottom border */\r\n}\r\n\r\n/* resizer (touch devices) */\r\n\r\n.fc-h-event.fc-selected .fc-resizer {\r\n  /* 8x8 little dot */\r\n  border-radius: 4px;\r\n  border-width: 1px;\r\n  width: 6px;\r\n  height: 6px;\r\n  border-style: solid;\r\n  border-color: inherit;\r\n  background: #fff;\r\n  /* vertically center */\r\n  top: 50%;\r\n  margin-top: -4px;\r\n}\r\n\r\n/* left resizer  */\r\n.fc-ltr .fc-h-event.fc-selected .fc-start-resizer,\r\n.fc-rtl .fc-h-event.fc-selected .fc-end-resizer {\r\n  margin-left: -4px; /* centers the 8x8 dot on the left edge */\r\n}\r\n\r\n/* right resizer */\r\n.fc-ltr .fc-h-event.fc-selected .fc-end-resizer,\r\n.fc-rtl .fc-h-event.fc-selected .fc-start-resizer {\r\n  margin-right: -4px; /* centers the 8x8 dot on the right edge */\r\n}\r\n\r\n/* DayGrid events\r\n----------------------------------------------------------------------------------------------------\r\nWe use the full \"fc-day-grid-event\" class instead of using descendants because the event won't\r\nbe a descendant of the grid when it is being dragged.\r\n*/\r\n\r\n.fc-day-grid-event {\r\n  margin: 2px 5px 0; /* spacing between events and edges */\r\n  padding: 0 1px;\r\n}\r\n\r\ntr:first-child > td > .fc-day-grid-event {\r\n  margin-top: 2px; /* a little bit more space before the first event */\r\n}\r\n\r\n.fc-day-grid-event.fc-selected:after {\r\n  content: \"\";\r\n  position: absolute;\r\n  z-index: 1; /* same z-index as fc-bg, behind text */\r\n  /* overcome the borders */\r\n  top: -1px;\r\n  right: -1px;\r\n  bottom: -1px;\r\n  left: -1px;\r\n  /* darkening effect */\r\n  background: #000;\r\n  opacity: 0.25;\r\n}\r\n\r\n.fc-day-grid-event .fc-content {\r\n  /* force events to be one-line tall */\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  color: white;\r\n  padding: 0 5px;\r\n}\r\n\r\n.fc-day-grid-event .fc-time {\r\n  font-weight: bold;\r\n}\r\n\r\n/* resizer (cursor devices) */\r\n\r\n/* left resizer  */\r\n.fc-ltr .fc-day-grid-event.fc-allow-mouse-resize .fc-start-resizer,\r\n.fc-rtl .fc-day-grid-event.fc-allow-mouse-resize .fc-end-resizer {\r\n  margin-left: -2px; /* to the day cell's edge */\r\n}\r\n\r\n/* right resizer */\r\n.fc-ltr .fc-day-grid-event.fc-allow-mouse-resize .fc-end-resizer,\r\n.fc-rtl .fc-day-grid-event.fc-allow-mouse-resize .fc-start-resizer {\r\n  margin-right: -2px; /* to the day cell's edge */\r\n}\r\n\r\n/* Event Limiting\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n/* \"more\" link that represents hidden events */\r\n\r\na.fc-more {\r\n  margin: 1px 3px;\r\n  font-size: 0.85em;\r\n  cursor: pointer;\r\n  text-decoration: none;\r\n}\r\n\r\na.fc-more:hover {\r\n  text-decoration: underline;\r\n}\r\n\r\n.fc-limited {\r\n  /* rows and cells that are hidden because of a \"more\" link */\r\n  display: none;\r\n}\r\n\r\n/* popover that appears when \"more\" link is clicked */\r\n\r\n.fc-day-grid .fc-row {\r\n  z-index: 1; /* make the \"more\" popover one higher than this */\r\n}\r\n\r\n.fc-more-popover {\r\n  z-index: 2;\r\n  width: 220px;\r\n}\r\n\r\n.fc-more-popover .fc-event-container {\r\n  padding: 10px;\r\n}\r\n\r\n/* Now Indicator\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-now-indicator {\r\n  position: absolute;\r\n  border: 0 solid red;\r\n}\r\n\r\n/* Utilities\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-unselectable {\r\n  -webkit-user-select: none;\r\n  -khtml-user-select: none;\r\n  -moz-user-select: none;\r\n  -ms-user-select: none;\r\n  user-select: none;\r\n  -webkit-touch-callout: none;\r\n  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);\r\n}\r\n\r\n/* Toolbar\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-toolbar {\r\n  text-align: center;\r\n  margin-bottom: 1em;\r\n}\r\n\r\n.fc-toolbar .fc-left {\r\n  float: left;\r\n}\r\n\r\n.fc-toolbar .fc-right {\r\n  float: right;\r\n}\r\n\r\n.fc-toolbar .fc-center {\r\n  display: inline-block;\r\n}\r\n\r\n/* the things within each left/right/center section */\r\n.fc .fc-toolbar > * > * {\r\n  /* extra precedence to override button border margins */\r\n  float: left;\r\n  margin-left: 0.75em;\r\n}\r\n\r\n/* the first thing within each left/center/right section */\r\n.fc .fc-toolbar > * > :first-child {\r\n  /* extra precedence to override button border margins */\r\n  margin-left: 0;\r\n}\r\n\r\n/* title text */\r\n\r\n.fc-toolbar h2 {\r\n  margin: 0;\r\n  font-size: 1.8em;\r\n}\r\n\r\n/* button layering (for border precedence) */\r\n\r\n.fc-toolbar button {\r\n  position: relative;\r\n}\r\n\r\n.fc-toolbar .fc-state-hover,\r\n.fc-toolbar .ui-state-hover {\r\n  z-index: 2;\r\n}\r\n\r\n.fc-toolbar .fc-state-down {\r\n  z-index: 3;\r\n}\r\n\r\n.fc-toolbar .fc-state-active,\r\n.fc-toolbar .ui-state-active {\r\n  z-index: 4;\r\n}\r\n\r\n.fc-toolbar button:focus {\r\n  z-index: 5;\r\n}\r\n\r\n/* View Structure\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n/* undo twitter bootstrap's box-sizing rules. normalizes positioning techniques */\r\n/* don't do this for the toolbar because we'll want bootstrap to style those buttons as some pt */\r\n.fc-view-container *,\r\n.fc-view-container *:before,\r\n.fc-view-container *:after {\r\n  -webkit-box-sizing: content-box;\r\n  -moz-box-sizing: content-box;\r\n  box-sizing: content-box;\r\n}\r\n\r\n.fc-view, /* scope positioning and z-index's for everything within the view */\r\n.fc-view > table {\r\n  /* so dragged elements can be above the view's main element */\r\n  position: relative;\r\n  z-index: 1;\r\n}\r\n\r\n/* BasicView\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n/* day row structure */\r\n\r\n.fc-basicWeek-view .fc-content-skeleton,\r\n.fc-basicDay-view .fc-content-skeleton {\r\n  /* there may be week numbers in these views, so no padding-top */\r\n  padding-bottom: 1em; /* ensure a space at bottom of cell for user selecting/clicking */\r\n}\r\n\r\n.fc-basic-view .fc-body .fc-row {\r\n  min-height: 4em; /* ensure that all rows are at least this tall */\r\n}\r\n\r\n/* a \"rigid\" row will take up a constant amount of height because content-skeleton is absolute */\r\n\r\n.fc-row.fc-rigid {\r\n  overflow: hidden;\r\n}\r\n\r\n.fc-row.fc-rigid .fc-content-skeleton {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n}\r\n\r\n/* week and day number styling */\r\n\r\n.fc-day-top.fc-other-month {\r\n  opacity: 0.3;\r\n}\r\n\r\n.fc-basic-view .fc-week-number,\r\n.fc-basic-view .fc-day-number {\r\n  padding: 2px;\r\n}\r\n\r\n.fc-basic-view th.fc-week-number,\r\n.fc-basic-view th.fc-day-number {\r\n  padding: 0 2px; /* column headers can't have as much v space */\r\n}\r\n\r\n.fc-ltr .fc-basic-view .fc-day-top .fc-day-number {\r\n  float: right;\r\n}\r\n.fc-rtl .fc-basic-view .fc-day-top .fc-day-number {\r\n  float: left;\r\n}\r\n\r\n.fc-ltr .fc-basic-view .fc-day-top .fc-week-number {\r\n  float: left;\r\n  border-radius: 0 0 3px 0;\r\n}\r\n.fc-rtl .fc-basic-view .fc-day-top .fc-week-number {\r\n  float: right;\r\n  border-radius: 0 0 0 3px;\r\n}\r\n\r\n.fc-basic-view .fc-day-top .fc-week-number {\r\n  min-width: 1.5em;\r\n  text-align: center;\r\n  background-color: #f2f2f2;\r\n  color: #808080;\r\n}\r\n\r\n/* when week/day number have own column */\r\n\r\n.fc-basic-view td.fc-week-number {\r\n  text-align: center;\r\n}\r\n\r\n.fc-basic-view td.fc-week-number > * {\r\n  /* work around the way we do column resizing and ensure a minimum width */\r\n  display: inline-block;\r\n  min-width: 1.25em;\r\n}\r\n\r\n/* AgendaView all-day area\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-agenda-view .fc-day-grid {\r\n  position: relative;\r\n  z-index: 2; /* so the \"more..\" popover will be over the time grid */\r\n}\r\n\r\n.fc-agenda-view .fc-day-grid .fc-row {\r\n  min-height: 3em; /* all-day section will never get shorter than this */\r\n}\r\n\r\n.fc-agenda-view .fc-day-grid .fc-row .fc-content-skeleton {\r\n  padding-bottom: 1em; /* give space underneath events for clicking/selecting days */\r\n}\r\n\r\n/* TimeGrid axis running down the side (for both the all-day area and the slot area)\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc .fc-axis {\r\n  /* .fc to overcome default cell styles */\r\n  vertical-align: middle;\r\n  padding: 0 4px;\r\n  white-space: nowrap;\r\n}\r\n\r\n.fc-ltr .fc-axis {\r\n  text-align: right;\r\n}\r\n\r\n.fc-rtl .fc-axis {\r\n  text-align: left;\r\n}\r\n\r\n.ui-widget td.fc-axis {\r\n  font-weight: normal; /* overcome jqui theme making it bold */\r\n}\r\n\r\n/* TimeGrid Structure\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-time-grid-container, /* so scroll container's z-index is below all-day */\r\n.fc-time-grid {\r\n  /* so slats/bg/content/etc positions get scoped within here */\r\n  position: relative;\r\n  z-index: 1;\r\n}\r\n\r\n.fc-time-grid {\r\n  min-height: 100%; /* so if height setting is 'auto', .fc-bg stretches to fill height */\r\n}\r\n\r\n.fc-time-grid table {\r\n  /* don't put outer borders on slats/bg/content/etc */\r\n  border: 0 hidden transparent;\r\n}\r\n\r\n.fc-time-grid > .fc-bg {\r\n  z-index: 1;\r\n}\r\n\r\n.fc-time-grid .fc-slats,\r\n.fc-time-grid > hr {\r\n  /* the <hr> AgendaView injects when grid is shorter than scroller */\r\n  position: relative;\r\n  z-index: 2;\r\n}\r\n\r\n.fc-time-grid .fc-content-col {\r\n  position: relative; /* because now-indicator lives directly inside */\r\n}\r\n\r\n.fc-time-grid .fc-content-skeleton {\r\n  position: absolute;\r\n  z-index: 3;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n}\r\n\r\n/* divs within a cell within the fc-content-skeleton */\r\n\r\n.fc-time-grid .fc-business-container {\r\n  position: relative;\r\n  z-index: 1;\r\n}\r\n\r\n.fc-time-grid .fc-bgevent-container {\r\n  position: relative;\r\n  z-index: 2;\r\n}\r\n\r\n.fc-time-grid .fc-highlight-container {\r\n  position: relative;\r\n  z-index: 3;\r\n}\r\n\r\n.fc-time-grid .fc-event-container {\r\n  position: relative;\r\n  z-index: 4;\r\n}\r\n\r\n.fc-time-grid .fc-now-indicator-line {\r\n  z-index: 5;\r\n}\r\n\r\n.fc-time-grid .fc-helper-container {\r\n  /* also is fc-event-container */\r\n  position: relative;\r\n  z-index: 6;\r\n}\r\n\r\n/* TimeGrid Slats (lines that run horizontally)\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-time-grid .fc-slats td {\r\n  height: 1.5em;\r\n  border-bottom: 0; /* each cell is responsible for its top border */\r\n}\r\n\r\n.fc-time-grid .fc-slats .fc-minor td {\r\n  border-top-style: dotted;\r\n}\r\n\r\n.fc-time-grid .fc-slats .ui-widget-content {\r\n  /* for jqui theme */\r\n  background: none; /* see through to fc-bg */\r\n}\r\n\r\n/* TimeGrid Highlighting Slots\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-time-grid .fc-highlight-container {\r\n  /* a div within a cell within the fc-highlight-skeleton */\r\n  position: relative; /* scopes the left/right of the fc-highlight to be in the column */\r\n}\r\n\r\n.fc-time-grid .fc-highlight {\r\n  position: absolute;\r\n  left: 0;\r\n  right: 0;\r\n  /* top and bottom will be in by JS */\r\n}\r\n\r\n/* TimeGrid Event Containment\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-ltr .fc-time-grid .fc-event-container {\r\n  /* space on the sides of events for LTR (default) */\r\n  margin: 0 2.5% 0 2px;\r\n}\r\n\r\n.fc-rtl .fc-time-grid .fc-event-container {\r\n  /* space on the sides of events for RTL */\r\n  margin: 0 2px 0 2.5%;\r\n}\r\n\r\n.fc-time-grid .fc-event,\r\n.fc-time-grid .fc-bgevent {\r\n  position: absolute;\r\n  z-index: 1; /* scope inner z-index's */\r\n}\r\n\r\n.fc-time-grid .fc-bgevent {\r\n  /* background events always span full width */\r\n  left: 0;\r\n  right: 0;\r\n}\r\n\r\n/* Generic Vertical Event\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-v-event.fc-not-start {\r\n  /* events that are continuing from another day */\r\n  /* replace space made by the top border with padding */\r\n  border-top-width: 0;\r\n  padding-top: 1px;\r\n\r\n  /* remove top rounded corners */\r\n  border-top-left-radius: 0;\r\n  border-top-right-radius: 0;\r\n}\r\n\r\n.fc-v-event.fc-not-end {\r\n  /* replace space made by the top border with padding */\r\n  border-bottom-width: 0;\r\n  padding-bottom: 1px;\r\n\r\n  /* remove bottom rounded corners */\r\n  border-bottom-left-radius: 0;\r\n  border-bottom-right-radius: 0;\r\n}\r\n\r\n/* TimeGrid Event Styling\r\n----------------------------------------------------------------------------------------------------\r\nWe use the full \"fc-time-grid-event\" class instead of using descendants because the event won't\r\nbe a descendant of the grid when it is being dragged.\r\n*/\r\n\r\n.fc-time-grid-event {\r\n  overflow: hidden; /* don't let the bg flow over rounded corners */\r\n}\r\n\r\n.fc-time-grid-event.fc-selected {\r\n  /* need to allow touch resizers to extend outside event's bounding box */\r\n  /* common fc-selected styles hide the fc-bg, so don't need this anyway */\r\n  overflow: visible;\r\n}\r\n\r\n.fc-time-grid-event.fc-selected .fc-bg {\r\n  display: none; /* hide semi-white background, to appear darker */\r\n}\r\n\r\n.fc-time-grid-event .fc-content {\r\n  overflow: hidden; /* for when .fc-selected */\r\n}\r\n\r\n.fc-time-grid-event .fc-time,\r\n.fc-time-grid-event .fc-title {\r\n  padding: 0 1px;\r\n}\r\n\r\n.fc-time-grid-event .fc-time {\r\n  font-size: 0.85em;\r\n  white-space: nowrap;\r\n}\r\n\r\n/* short mode, where time and title are on the same line */\r\n\r\n.fc-time-grid-event.fc-short .fc-content {\r\n  /* don't wrap to second line (now that contents will be inline) */\r\n  white-space: nowrap;\r\n}\r\n\r\n.fc-time-grid-event.fc-short .fc-time,\r\n.fc-time-grid-event.fc-short .fc-title {\r\n  /* put the time and title on the same line */\r\n  display: inline-block;\r\n  vertical-align: top;\r\n}\r\n\r\n.fc-time-grid-event.fc-short .fc-time span {\r\n  display: none; /* don't display the full time text... */\r\n}\r\n\r\n.fc-time-grid-event.fc-short .fc-time:before {\r\n  content: attr(data-start); /* ...instead, display only the start time */\r\n}\r\n\r\n.fc-time-grid-event.fc-short .fc-time:after {\r\n  content: \"\\000A0-\\000A0\"; /* seperate with a dash, wrapped in nbsp's */\r\n}\r\n\r\n.fc-time-grid-event.fc-short .fc-title {\r\n  font-size: 0.85em; /* make the title text the same size as the time */\r\n  padding: 0; /* undo padding from above */\r\n}\r\n\r\n/* resizer (cursor device) */\r\n\r\n.fc-time-grid-event.fc-allow-mouse-resize .fc-resizer {\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  height: 8px;\r\n  overflow: hidden;\r\n  line-height: 8px;\r\n  font-size: 11px;\r\n  font-family: monospace;\r\n  text-align: center;\r\n  cursor: s-resize;\r\n}\r\n\r\n.fc-time-grid-event.fc-allow-mouse-resize .fc-resizer:after {\r\n  content: \"=\";\r\n}\r\n\r\n/* resizer (touch device) */\r\n\r\n.fc-time-grid-event.fc-selected .fc-resizer {\r\n  /* 10x10 dot */\r\n  border-radius: 5px;\r\n  border-width: 1px;\r\n  width: 8px;\r\n  height: 8px;\r\n  border-style: solid;\r\n  border-color: inherit;\r\n  background: #fff;\r\n  /* horizontally center */\r\n  left: 50%;\r\n  margin-left: -5px;\r\n  /* center on the bottom edge */\r\n  bottom: -5px;\r\n}\r\n\r\n/* Now Indicator\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n.fc-time-grid .fc-now-indicator-line {\r\n  border-top-width: 1px;\r\n  left: 0;\r\n  right: 0;\r\n}\r\n\r\n/* arrow on axis */\r\n\r\n.fc-time-grid .fc-now-indicator-arrow {\r\n  margin-top: -5px; /* vertically center on top coordinate */\r\n}\r\n\r\n.fc-ltr .fc-time-grid .fc-now-indicator-arrow {\r\n  left: 0;\r\n  /* triangle pointing right... */\r\n  border-width: 5px 0 5px 6px;\r\n  border-top-color: transparent;\r\n  border-bottom-color: transparent;\r\n}\r\n\r\n.fc-rtl .fc-time-grid .fc-now-indicator-arrow {\r\n  right: 0;\r\n  /* triangle pointing left... */\r\n  border-width: 5px 6px 5px 0;\r\n  border-top-color: transparent;\r\n  border-bottom-color: transparent;\r\n}\r\n\r\n/* List View\r\n--------------------------------------------------------------------------------------------------*/\r\n\r\n/* possibly reusable */\r\n\r\n.fc-event-dot {\r\n  display: inline-block;\r\n  width: 10px;\r\n  height: 10px;\r\n  border-radius: 5px;\r\n}\r\n\r\n/* view wrapper */\r\n\r\n.fc-rtl .fc-list-view {\r\n  direction: rtl; /* unlike core views, leverage browser RTL */\r\n}\r\n\r\n.fc-list-view {\r\n  border-width: 1px;\r\n  border-style: solid;\r\n}\r\n\r\n/* table resets */\r\n\r\n.fc .fc-list-table {\r\n  table-layout: auto; /* for shrinkwrapping cell content */\r\n}\r\n\r\n.fc-list-table td {\r\n  border-width: 1px 0 0;\r\n  padding: 8px 14px;\r\n}\r\n\r\n.fc-list-table tr:first-child td {\r\n  border-top-width: 0;\r\n}\r\n\r\n/* day headings with the list */\r\n\r\n.fc-list-heading {\r\n  border-bottom-width: 1px;\r\n}\r\n\r\n.fc-list-heading td {\r\n  font-weight: bold;\r\n}\r\n\r\n.fc-ltr .fc-list-heading-main {\r\n  float: left;\r\n}\r\n.fc-ltr .fc-list-heading-alt {\r\n  float: right;\r\n}\r\n\r\n.fc-rtl .fc-list-heading-main {\r\n  float: right;\r\n}\r\n.fc-rtl .fc-list-heading-alt {\r\n  float: left;\r\n}\r\n\r\n/* event list items */\r\n\r\n.fc-list-item.fc-has-url {\r\n  cursor: pointer; /* whole row will be clickable */\r\n}\r\n\r\n.fc-list-item:hover td {\r\n  background-color: #f5f5f5;\r\n}\r\n\r\n.fc-list-item-marker,\r\n.fc-list-item-time {\r\n  white-space: nowrap;\r\n  width: 1px;\r\n}\r\n\r\n/* make the dot closer to the event title */\r\n.fc-ltr .fc-list-item-marker {\r\n  padding-right: 0;\r\n}\r\n.fc-rtl .fc-list-item-marker {\r\n  padding-left: 0;\r\n}\r\n\r\n.fc-list-item-title a {\r\n  /* every event title cell has an <a> tag */\r\n  text-decoration: none;\r\n  color: inherit;\r\n}\r\n\r\n.fc-list-item-title a[href]:hover {\r\n  /* hover effect only on titles with hrefs */\r\n  text-decoration: underline;\r\n}\r\n\r\n/* message when no events */\r\n\r\n.fc-list-empty-wrap2 {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n}\r\n\r\n.fc-list-empty-wrap1 {\r\n  width: 100%;\r\n  height: 100%;\r\n  display: table;\r\n}\r\n\r\n.fc-list-empty {\